.sxa-breadcrumb {
  padding: 15px 0;
  background: var(--bg-surface);
  border-bottom: 1px solid var(--border-color);
}
.sxa-breadcrumb .breadcrumb-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 8px;
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 14px;
}
.sxa-breadcrumb .breadcrumb-item {
  display: flex;
  align-items: center;
  color: var(--text-secondary);
}
.sxa-breadcrumb .breadcrumb-item a {
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 0.15s ease-in-out;
}
.sxa-breadcrumb .breadcrumb-item a:hover {
  color: var(--color-primary);
  text-decoration: underline;
}
.sxa-breadcrumb .breadcrumb-item.active {
  color: var(--text-primary);
  font-weight: 600;
}
.sxa-breadcrumb .breadcrumb-item:not(:last-child)::after {
  content: "/";
  margin-left: 8px;
  color: var(--text-muted);
  font-weight: 400;
}
.sxa-breadcrumb[data-variant=compact] {
  padding: 10px 0;
}
.sxa-breadcrumb[data-variant=compact] .breadcrumb-list {
  font-size: 13px;
  gap: 6px;
}
.sxa-breadcrumb[data-variant=compact] .breadcrumb-item:not(:last-child)::after {
  margin-left: 6px;
}
.sxa-breadcrumb[data-variant=icon] .breadcrumb-item:not(:last-child)::after {
  content: "‚Ä∫";
  font-size: 18px;
  font-weight: 700;
}
.sxa-breadcrumb[data-variant=icon] .breadcrumb-item:first-child::before {
  content: "üè†";
  margin-right: 6px;
}

@media (max-width: 576px) {
  .sxa-breadcrumb .breadcrumb-list {
    font-size: 12px;
  }
  .sxa-breadcrumb .breadcrumb-item:not(:first-child):not(:last-child) {
    display: none;
  }
  .sxa-breadcrumb .breadcrumb-item:nth-last-child(2)::after {
    content: "...";
  }
