: 8px;\r\n            color: var(--text-muted);\r\n            font-weight: $font-weight-normal;\r\n        }\r\n    }\r\n\r\n    // Compact Variant\r\n    &[data-variant=\"compact\"] {\r\n        padding: 10px 0;\r\n\r\n        .breadcrumb-list {\r\n            font-size: 13px;\r\n            gap: 6px;\r\n        }\r\n\r\n        .breadcrumb-item:not(:last-child)::after {\r\n            margin-left: 6px;\r\n        }\r\n    }\r\n\r\n    // Icon-based Variant\r\n    &[data-variant=\"icon\"] {\r\n        .breadcrumb-item {\r\n            &:not(:last-child)::after {\r\n                content: '‚Ä∫';\r\n                font-size: 18px;\r\n                font-weight: $font-weight-bold;\r\n            }\r\n\r\n            &:first-child::before {\r\n                content: 'üè†';\r\n                margin-right: 6px;\r\n            }\r\n        }\r\n    }\r\n}\r\n\r\n// Responsive\r\n@media (max-width: $breakpoint-sm) {\r\n    .sxa-breadcrumb {\r\n        .breadcrumb-list {\r\n            font-size: 12px;\r\n        }\r\n\r\n        // Hide middle items on mobile, show only first and last\r\n        .breadcrumb-item:not(:first-child):not(:last-child) {\r\n            display: none;\r\n        }\r\n\r\n        .breadcrumb-item:nth-last-child(2)::after {\r\n            content: '...';\r\n        }\r\n    }\r\n}","// ============================================\r\n// SITECORE SXA COMPONENT LIBRARY - VARIABLES\r\n// ============================================\r\n\r\n// Breakpoints (Bootstrap-compatible)\r\n$breakpoint-xs: 0;\r\n$breakpoint-sm: 576px;\r\n$breakpoint-md: 768px;\r\n$breakpoint-lg: 992px;\r\n$breakpoint-xl: 1200px;\r\n$breakpoint-xxl: 1400px;\r\n\r\n// Container widths\r\n$container-max-widths: (\r\n  sm: 540px,\r\n  md: 720px,\r\n  lg: 960px,\r\n  xl: 1140px,\r\n  xxl: 1320px\r\n);\r\n\r\n// Spacing scale\r\n$spacer: 1rem;\r\n$spacers: (\r\n  0: 0,\r\n  1: $spacer * 0.25,\r\n  2: $spacer * 0.5,\r\n  3: $spacer,\r\n  4: $spacer * 1.5,\r\n  5: $spacer * 3,\r\n  6: $spacer * 4,\r\n  7: $spacer * 5,\r\n);\r\n\r\n// Typography\r\n$font-family-base: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\r\n$font-family-headings: 'Georgia', serif;\r\n\r\n$font-size-base: 1rem;\r\n$font-size-sm: 0.875rem;\r\n$font-size-lg: 1.125rem;\r\n\r\n$font-weight-light: 300;\r\n$font-weight-normal: 400;\r\n$font-weight-semibold: 600;\r\n$font-weight-bold: 700;\r\n\r\n$line-height-base: 1.6;\r\n$line-height-sm: 1.4;\r\n$line-height-lg: 1.8;\r\n\r\n$h1-font-size: 2.5rem;\r\n$h2-font-size: 2rem;\r\n$h3-font-size: 1.75rem;\r\n$h4-font-size: 1.5rem;\r\n$h5-font-size: 1.25rem;\r\n$h6-font-size: 1rem;\r\n\r\n// Border radius\r\n$border-radius-sm: 0.25rem;\r\n$border-radius: 0.375rem;\r\n$border-radius-lg: 0.5rem;\r\n$border-radius-xl: 1rem;\r\n\r\n// Shadows\r\n$box-shadow-sm: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);\r\n$box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);\r\n$box-shadow-lg: 0 1rem 3rem rgba(0, 0, 0, 0.175);\r\n\r\n// Transitions\r\n$transition-base: all 0.3s ease-in-out;\r\n$transition-fast: all 0.15s ease-in-out;\r\n$transition-slow: all 0.5s ease-in-out;\r\n\r\n// Z-index layers\r\n$zindex-dropdown: 1000;\r\n$zindex-sticky: 1020;\r\n$zindex-fixed: 1030;\r\n$zindex-modal-backdrop: 1040;\r\n$zindex-modal: 1050;\r\n$zindex-popover: 1060;\r\n$zindex-tooltip: 1070;\r\n$zindex-experience-editor: 9000;\r\n\r\n// Grid\r\n$grid-columns: 12;\r\n$grid-gutter-width: 1.5rem;\r\n\r\n// Component-specific variables\r\n$header-height: 80px;\r\n$header-height-mobile: 60px;\r\n$footer-height: 300px;\r\n\r\n// Experience Editor\r\n$ee-placeholder-border: 2px dashed #0078d4;\r\n$ee-component-border: 1px solid #0078d4;\r\n$ee-hover-bg: rgba(0, 120, 212, 0.1);\r\n$ee-toolbar-height: 50px;\r\n"],"sourceRoot":""}]);
